import React, { Component } from 'react';
import Table from './Table'
import Form from './Form'

class App extends Component {
    state = {
        shopList: []
    };

    removeItem = index => {
        const { shopList } = this.state
        this.setState({
            shopList: shopList.filter((item, i) => {
                return i !== index
            }),
        })
    }

    handleSubmit = item => {
        this.setState({ shopList: [...this.state.shopList, item] })
    }

    render() {
        const { shopList } = this.state;
        return (
            <div className="container">
                <Table shopListData={shopList} removeItem={this.removeItem} />
                <Form handleSubmit={this.handleSubmit}/>
            </div>
        )
    }
}

export default App;