import React, {Component} from 'react';

const TableHead = () => {
    return (
        <thead>
              <tr>
                <th>Name</th>
                <th>Count</th>
                <th></th>
              </tr>
        </thead>
    )
}
const TableBody = props => {
    const rows = props.shopListData.map((row, index)=>{
        return(
            <tr key={index}>
                <td>{row.name}</td>
                <td>{row.count}</td>
                <td>
                    <button onClick={()=>props.removeItem(index)}>Delete</button>
                </td>
            </tr>
        )
    });
    return (
        <tbody>{rows}</tbody>
    );

}


class Table extends Component {
    
    render() {
       const {shopListData, removeItem } = this.props
        return (
            <table>
                <TableHead/>
                <TableBody shopListData={shopListData} removeItem={removeItem} />
          </table>
        );
    }
}

export default Table;